  .cookie-wrapper-disabled {
    display: none;
  }
  
  
  .otp-main-block::placeholder {
    position: relative;
    top: 2px;
  }
  .cs-Gallery {
    display: flex;
    flex-direction: row;
  }
  .cs-Gallery .m-img-block img {
    height: auto;
    width: 100%;
    max-width: 133px !important;
    max-height: 82px !important;
  }
  
  .product-image-pp {
    max-width: 567px;
  }
  
  
  
  /* OU Preloder */
  #preloader {
    position: absolute;
    width: 100%;
    height: 100vh;
    margin: auto;
    opacity: 1;
    visibility: visible;
    flex-direction: column;
  }
  #preloader ~ body {
    overflow: hidden;
  }
  .ceHfpt {
    z-index: 9999;
    position: fixed;
    top: 0px;
    left: 0px;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.3s linear 0s, visibility 0.2s linear 0.3s;
    height: 100vh;
    width: 100vw;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    display: flex;
    background: #f8f7f2;
  }
  .gNkidG {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 8px solid #101923;
    animation: 2s ease 0s infinite normal none running kBruCf;
  }
  .gNkidG span {
    vertical-align: top;
    display: inline-block;
    width: 42px;
    background-color: #101923;
    animation: 2s ease-in 0s infinite normal none running dMiuQA;
  }
  .preloader-text {
    margin-top: 34px;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
  }
  @keyframes kBruCf {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(180deg);
    }
    50% {
        transform: rotate(180deg);
    }
    75% {
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(360deg);
    }
  }
  @keyframes dMiuQA {
    0% {
        height: 0%;
    }
    25% {
        height: 0%;
    }
    50% {
        height: 100%;
    }
    75% {
        height: 75%;
    }
    100% {
        height: 0%;
    }
  }
  .disabled-element-cs {
    display: none;
  }
  .user-message-cs {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  
  /* Orders tab in profile page */
  .profile-order-tab {
    padding: 0.3rem 1rem;
    color: rgba(52, 52, 52, 0.75);
    cursor: pointer;
  }
  .profile-order-tab span {
    color: rgba(52, 52, 52, 0.75);
  }
  .profile-active-order-tab {    
    padding: 0.3rem 1rem;
    border-radius: 40px;
    background-color: #f8f7f2;
    cursor: pointer;
  }
  .profile-active-order-tab span {
    color: unset !important;
  }
  .disable-element-cs {
    display: none;
  }
  .non-elem-denter {
    display: flex;
    width: 100%;
    justify-content: center;
  }
  .mobile-menu {
    display: none;
  }
  .mobile-menu-active {
    max-height: 1500px;
  }
  .subCatItems {
    display: block !important;
    position: static;
    overflow: hidden;
    min-width: auto;
    background-color: #fff;
    max-height: 0;
    transition: .5s all ease-in-out;
  }
  .active-sub-cat {
    display: block !important;
    max-height: 500px;
  }
  .ExpandBtn {
    transition-property: all;
    transition-duration: 0.25s;
    transition-timing-function: all ease-in-out;
  }
  .ExpandBtn svg path:nth-child(1)  {
    transition: 0.4s all ease-in-out;
  }
  .SubCategoryExpanded {
    transform: rotate(-90deg);
  }
  .SubCategoryExpanded svg path:nth-child(1) {
    opacity: 0;
  }
  .story-grid::-webkit-scrollbar {
    width: 0;
    background: transparent;
  }
  .basket-header {
    position: relative;
    display: -webkit-box;
